import { Component, OnInit, Output, Input, EventEmitter, OnChanges, OnDestroy } from '@angular/core';
import { TeleMarketingBean } from '../../bean/telemarketing.bean';
import { Commfunc } from '../../../../../../@uisftech/common/service/commonFun';
import { TeleMarketingHttpService } from '../../http/telemarketing.http.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Message } from 'primeng/primeng';

@Component({
    selector: 'contact-record-module',
    templateUrl: './contact-record-module.component.html',
    styleUrls: ['./contact-record-module.component.scss'],
    providers: [Commfunc]
})
// -
export class ContactRecordDeploy implements OnInit, OnChanges, OnDestroy {

    teleMarketingBean: TeleMarketingBean = new TeleMarketingBean();
    @Input() inValue: any;
    // @Input() inValue2: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    custType1: string;
    custType2: string;
    custType3: string;
    custType4: string;
    custType5: string;
    Property1: boolean = false;
    Property2: boolean = false;
    Property3: boolean = false;
    code: any;
    AnotConnect: any = [];//
    areSponse: any = [];//
    isOperte: boolean = false;//
    isShow: boolean = false;//
    isShow2: boolean = false;
    isShow3: boolean = false;
    resultRecord: any = {};
    isShowCode: boolean = false;//
    isShowPhone: boolean = false;
    areSponse1: any = [];
    dataList: any;
    areSponse2: any = [];
    msgs: Message[] = [];
    constructor(
        public commfunc: Commfunc,
        public teleMarketingHttpService: TeleMarketingHttpService,
        public activatedRoute: ActivatedRoute,
        public router: Router,
    ) {
        this.codeValues() //
        this.AnotConnect = this.code['AnotConnec'];
        this.areSponse = this.code['AreSponse']
        this.areSponse1 = this.code['AreSponse1'];
        this.areSponse2 = this.code['AreSponse2'];
        this.dataList = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap)

    }

    ngOnInit() {
        debugger
        if (this.commfunc.getSessionDataCH('markFlag') == '1') {
            //
            this.teleMarketingBean.acall = '1';//-
            this.teleMarketingBean.aisconnect = '1';//-
            if (this.dataList.workEffortParentId == '01') {//
                this.teleMarketingBean.messToManger = '0';//-
                this.teleMarketingBean.messToPla = '0';//-
                this.teleMarketingBean.messToCus = '0';//-
                // this.changeCustType('1');//
                // this.changeCustType('3');//
            } else if (this.dataList.workEffortParentId == '16') {//
                this.teleMarketingBean.messToManger = '0';//-
                this.teleMarketingBean.messToPla = '0';
                this.teleMarketingBean.messToCus = '0';
            } else if (this.dataList.workEffortParentId == '17') {//
                this.teleMarketingBean.messToManger = '0';//-
                this.teleMarketingBean.messToPla = '0';
                this.teleMarketingBean.messToCus = '0';
            }
            // this.changeCustType('5');//
            this.changeCustType('');
            this.change();//
        } else {
            this.echo();
        }

        this.obPhoneWatcher.run();
    }
    ngOnChanges() {
        this.echo();
        // this.ManageNote();
    }

    //
    changeCustType(item) {
        debugger
        if (item == '1' && this.teleMarketingBean.messToManger == '1') {//-
            this.ManageNote();
        } else if (item == '2' && this.teleMarketingBean.messToManger == '0') {//-
            this.teleMarketingBean.messToMangerCon = '';
        }

        if (item == '3' && this.teleMarketingBean.messToPla == '1' && !this.teleMarketingBean.messToPlaCon) {//-
            if (this.dataList.obCustomercode && this.dataList.obCustomercode != 'undefined') {

            } else {
                this.dataList.obCustomercode = '';
            }
            if (this.dataList.obName && this.dataList.obName != 'undefined') {

            } else {
                this.dataList.obName = '';
            }
            if (this.dataList.obPhone && this.dataList.obPhone != '***') {

            } else {
                this.dataList.obPhone = '';
            }
            this.teleMarketingBean.messToPlaCon = "" + this.dataList.obCustomercode + "," + this.dataList.obName + "," + this.dataList.obPhone;//

        } else if (item == '4' && this.teleMarketingBean.messToPla == '0') {//-
            this.teleMarketingBean.messToPlaCon = '';
        }
        // if (this.dataList.workEffortTypeId=='16'&&item == '5' && this.teleMarketingBean.messToCus == '1') {//
        //     this.customerNote('');
        // }else if (this.dataList.workEffortTypeId!='16'&&item == '5' && this.teleMarketingBean.messToCus == '1') {//-
        //     this.customerNote('0020');
        // } else if (item == '6' && this.teleMarketingBean.messToCus == '0') {//-
        //     this.teleMarketingBean.messToCusCon = '';
        // }
        // 
        if (item == '' && this.teleMarketingBean.acall == '1' && this.teleMarketingBean.aisconnect == '1') {
            this.isOperte = true;
            this.isShowCode = false;
            this.isShowPhone = false;
            this.teleMarketingBean.aresponse = null;//
            this.teleMarketingBean.anotconnect = null;//
            // 
        } else if (item == '' && this.teleMarketingBean.acall == '1' && this.teleMarketingBean.aisconnect == '2') {//
            this.isOperte = false;
            this.isShowPhone = true;
            this.teleMarketingBean.aresponse = this.areSponse2[this.areSponse2.length - 1].value;//
            //
        } else if (item == '' && this.teleMarketingBean.acall == '0') {
            this.teleMarketingBean.aisconnect = '2';
            this.isOperte = true;
            this.isShowCode = true;//
            this.isShowPhone = false;
            this.teleMarketingBean.aresponse = null;//
            this.teleMarketingBean.anotconnect = null;//
        }

    }
    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code
    }
    echo() {
        debugger
        if (this.inValue && this.inValue.showflag == '1' && this.commfunc.getSessionDataCH('markFlag') == '1') {//
            this.outValue.emit(this.teleMarketingBean);
        }
        if (this.commfunc.getSessionDataCH('markFlag') == '2') {//()
            this.isOperte = true;
            this.isShow = true;
            if (this.teleMarketingBean.anotconnect) {//
                //
                this.isShowPhone = true;
            }
            // 
            if (this.teleMarketingBean.acall == '1' && this.teleMarketingBean.aisconnect == '1') {
                this.isShowCode = false;
                this.isShowPhone = false;
                // 
            } else if (this.teleMarketingBean.acall == '0' && this.teleMarketingBean.aisconnect == '2') {
                this.isShowCode = true;//
                this.isShowPhone = false;
            }
            this.resultRecord = JSON.parse(JSON.parse(this.commfunc.getSessionDataCH('dxlc')).dataMap);
            this.teleMarketingBean = this.resultRecord;
            console.log(this.teleMarketingBean);
        }
    }
    change() {
        if (this.dataList.workEffortParentId == '01' || this.dataList.workEffortParentId == '20') {
            this.isShow2 = true;
            this.teleMarketingBean.messToCus = '0';
        }
        if (this.dataList.workEffortParentId == '16' || this.dataList.workEffortParentId == '21') {
            this.isShow3 = true;
        }
        if (this.dataList.workEffortParentId == '17' || this.dataList.workEffortParentId == '22') {//()
            this.isShow2 = true;
            this.isShow3 = true;
            if (this.teleMarketingBean.aresponse == '0401') {//
                this.teleMarketingBean.messToCus = '1';
            } else {
                this.teleMarketingBean.messToCus = '0';
            }
        }
    }
    ///
    ManageNote() {
        this.teleMarketingHttpService.selectMsgTemplate({ templateId: '0022' }).subscribe(data => {
            if (data.returnCode.code == 'success') {
                let str = data.resultList[0].msgContent;
                if (this.dataList.obCustomercode) {

                } else {
                    this.dataList.obCustomercode = '';
                }
                if (this.dataList.obName) {

                } else {
                    this.dataList.obName = '';
                }
                if (this.dataList.obPhone && this.dataList.obPhone != '***') {

                } else {
                    this.dataList.obPhone = '';
                }
                let t = str.replace(/\[custNo\]/g, this.dataList.obCustomercode)
                    .replace(/\[custName\]/g, this.dataList.obName)
                    .replace(/\[custPhoneNo\]/g, this.dataList.obPhone);//
                this.teleMarketingBean.messToMangerCon = t;
            }

        })
    }
    // customerNote(template) {
    //     this.teleMarketingHttpService.selectMsgTemplate({ templateId: template }).subscribe(data => {
    //         if (data.returnCode.code == 'success') {
    //             let str = data.resultList[0].msgContent;
    //             let t = str.replace(/\[custNo\]/g, this.dataList.obCustomercode)
    //                 .replace(/\[custName\]/g, this.dataList.obName)
    //                 .replace(/\[custPhoneNo\]/g, this.dataList.obPhone);//
    //             this.teleMarketingBean.messToCusCon = t;
    //         }

    //     })
    // }
    //
    obPhoneWatcher = {
        parent: this,
        run() {
            this.init();
            this.watch();
        },//
        init() {
            this.phoneNumber = this.current();
        },//
        current() {
            let ret = '';
            let dataMap = JSON.parse(JSON.parse(this.parent.commfunc.getSessionDataCH('dxlc')).dataMap);
            ret = dataMap.obPhone;
            return ret;
        },
        phoneNumber: '',
        handle: null,
        watch() {
            this.handle = setInterval(() => {
                let current = this.current();//
                if (current != this.phoneNumber) {//
                    this.outputData();
                    this.phoneNumber = current;
                }
            }, 50);
        },
        outputData() {
            this.parent.dataList.obPhone = this.current();//
            this.parent.ManageNote();
            if (this.parent.dataList.obCustomercode && this.parent.dataList.obCustomercode != 'undefined') {

            } else {
                this.parent.dataList.obCustomercode = '';
            }
            if (this.parent.dataList.obName && this.parent.dataList.obName != 'undefined') {

            } else {
                this.parent.dataList.obName = '';
            }
            if (this.parent.dataList.obPhone && this.parent.dataList.obPhone != '***') {

            } else {
                this.parent.dataList.obPhone = '';
            }
            this.parent.teleMarketingBean.messToPlaCon = "" + this.parent.dataList.obCustomercode + "," + this.parent.dataList.obName + "," + this.parent.dataList.obPhone;//
        },
        stopWatching() {
            if (this.handle) {
                clearInterval(this.handle);//
            }
        }
    }

    ngOnDestroy(): void {
        this.obPhoneWatcher.stopWatching();//
    }
}
